  -webkit-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.loop {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-justify-content: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
}
.loop > div {
  -webkit-flex: 1;
      -ms-flex: 1;
          flex: 1;
