a.button {
  width: 3em;
  height: 3em;
  border-radius: 1.5em;
  line-height: 3em;
  display: inline-block;
  color: #fff;
  background-color: #4fc08d;
  transition: all 0.15s ease;
  box-sizing: border-box;
  border: 1px solid #4fc08d;
}
a.button.white {
  background-color: #fff;
